.body_usuario {
    background: linear-gradient(135deg, #0d6efd, #003a75);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }

  @keyframes fadeIn {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
  }